.trainers {
  padding: 6px 103px 105px 103px;
  position: relative;

  @include vp-1199 {
    padding: 19px 101px 100px 101px;
  }

  @include vp-767 {
    padding: 2px 47px 69px;
  }
}

.trainers h2 {
  font-family: $ff-alumni;
  font-size: 54px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.62px;
  margin: 0;
  padding: 0;
  margin-bottom: 48px;
  text-align: left;
  margin-left: 2px;


  @include vp-1199 {
    margin: 0;
    font-size: 42px;
    text-align: center;
    margin-bottom: 32px;
  }

  @include vp-767 {
    margin-right: 3px;
    font-size: 32px;
    margin-bottom: 43px;
  }
}

.trainers__swiper {
  max-width: 1160px;

  @include vp-1199 {
    max-width: 566px;
  }

  @include vp-767 {
    max-width: 226px;
  }
}

.trainers__wrapper {
  position: relative;
}

.trainers__swiper-slide {
  max-width: 260px;

  @include vp-1199 {
    max-width: 268px;
  }

  @include vp-767 {
    max-width: 226px;
  }
}

.trainers__name {
  margin: 0;
  padding: 0;
  color: $color-default-white;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 27px;
  position: absolute;
  text-align: center;
  bottom: 9px;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 100%;
  z-index: 4;
}

.trainers__swiper-slide:hover .trainers__name {
  bottom: 202px;

  @include vp-767 {
    bottom: 192px;
  }
}

.trainers__name p {
  margin: 0;
  padding: 0;
}

.trainers__extra-information {
  display: none;
}

.trainers__swiper-slide:hover .trainers__extra-information {
  padding: 74px 0 58px;
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(16, 38, 84, 0.89);
  z-index: 2;
  top: 0;
  color: $color-default-white;

  @include vp-767 {
    padding: 64px 0 58px;
  }
}

.trainers__sport {
  margin: 0;
  padding: 0;
  text-align: center;
  color: rgba(182, 189, 204, 1);
  margin-bottom: 33px;
}

.trainers__sport p {
  margin: 0;
  padding: 0;
}

.trainers__list-advantages {
  margin: 0;
  padding: 0;
  text-align: start;
  margin-left: 55px;
  list-style-type: none;
  overflow: auto;
  height: 105px;

  @include vp-767 {
    margin-left: 34px;
  }
}

.trainers__list-advantages li {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 21px;
  margin-bottom: 13px;
  max-width: 200px;
  align-items: baseline;
  display: grid;
  grid-template-columns: 6px auto;
  gap: 10px;


  &::before {
    content: "";
    width: 6px;
    height: 6px;
    background-color: $color-default-white;
    border-radius: 50%;
    display: block;
  }
}

.trainers__button-prev {
  stroke: rgba(175, 175, 175, 1);
  width: 56px;
  height: 56px;
  border-radius: 50%;
  left: 27px;

  &::after {
    content: none;
  }

  &:disabled {
    stroke: rgba(175, 175, 175, 1);
  }

  &:hover {
    border: 1px solid $color-dark-blue;
    stroke: $color-dark-blue;
  }

  &:focus {
    border: 1px solid $color-dark-blue;
    stroke: $color-dark-blue;
    outline: none;
  }

  &:active {
    background-color: $color-dark-blue;
    stroke: $color-default-white;
  }

  @include vp-1199 {
    left: 25px;
    top: 48%;
  }

  @include vp-767 {
    left: 3px;
    top: 52%;
    width: 36px;
    height: 36px;
  }
}

.trainers__button-next {
  stroke: rgba(175, 175, 175, 1);
  width: 56px;
  height: 56px;
  border-radius: 50%;
  right: 27px;

  &::after {
    content: none;
  }

  &:hover {
    border: 1px solid $color-dark-blue;
    stroke: $color-dark-blue;
  }

  &:focus {
    border: 1px solid $color-dark-blue;
    stroke: $color-dark-blue;
    outline: none;
  }

  &:active {
    background-color: $color-dark-blue;
    stroke: $color-default-white;
  }

  @include vp-1199 {
    right: 25px;
    top: 48%;
  }

  @include vp-767 {
    right: 3px;
    top: 52%;
    width: 36px;
    height: 36px;
  }
}

.trainers__icon-prev {
  @include vp-767 {
    width: 10px;
    height: 19px;
  }
}

.trainers__icon-next {
  @include vp-767 {
    width: 10px;
    height: 19px;
  }
}

.trainers__img img {
  @include vp-1199 {
    width: 268px;
    height: 294px;
  }

  @include vp-767 {
    width: 226px;
    height: 274px;
  }
}
